<template>
	<div>
		<el-button @click="roleAdd" style="margin-bottom: 8px;"></el-button>
		<div class="block">
			<el-pagination
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page.sync="currentPage2"
				:page-sizes="[4, 5, 6, 7]"
				:page-size="pageSize"
				layout="sizes, prev, pager, next"
				:total="total"
			></el-pagination>
		</div>
		<el-table :data="cRoleData" style="width: 100%">
			<el-table-column prop="userName" label=""></el-table-column>
			<el-table-column prop="userRealName" label=""></el-table-column>
			<el-table-column prop="userPhone" label=""></el-table-column>
			<el-table-column prop="auth" label="" width="600"></el-table-column>
			<el-table-column prop="userUseWangwang" label=""></el-table-column>
			<el-table-column fixed="right" label="">
				<template slot-scope="scope">
					<el-button @click="handleClick(scope.row)" type="text" size="small">
						
					</el-button>
					<el-button
						type="text"
						size="small"
						@click.native.prevent="deleteRow(scope.$index, cRoleData)"
					>
						
					</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="form">
				<el-form-item label="" label-width="120px">
					<el-input v-model="form.userName" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="120px">
					<el-input v-model="form.userRealName" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="120px">
					<el-checkbox v-model="form.userSaleRank"></el-checkbox>
					<el-checkbox v-model="form.userProRank"></el-checkbox>
					<el-checkbox v-model="form.userSendRank"></el-checkbox>
					<el-checkbox v-model="form.userExpressRank"></el-checkbox>
					<el-checkbox v-model="form.userQualityRank"></el-checkbox>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false"> </el-button>
				<el-button type="primary" @click="subFrom"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
import { roleList } from '@/api/index.js';
export default {
	data() {
		return {
			roleData: [],
			dialogFormVisible: false,
			currentPage2: 1,
			pageSize: 4,
			total: 14,
			form: {
				userName: '',
				userRealName: '',
				userSaleRank: false,
				userProRank: false,
				userSendRank: false,
				userExpressRank: false,
				userQualityRank: false
			}
		};
	},
	computed: {
		cRoleData() {
			let startNum = this.pageSize * (this.currentPage2 - 1);
			let data = this.roleData;
			return data.slice(startNum, startNum + this.pageSize);
		}
	},
	methods: {
		handleSizeChange(val) {
			this.pageSize = val;
		},
		handleCurrentChange(val) {
			this.currentPage2 = val;
		},
		handleClick(row) {
			this.dialogFormVisible = true;
			this.form.userName = row.userName;
			this.form.userRealName = row.userRealName;
			row.userSaleRank == 'Y'
				? (this.form.userSaleRank = true)
				: (this.form.userSaleRank = false);
			row.userProRank == 'Y'
				? (this.form.userProRank = true)
				: (this.form.userProRank = false);
			row.userSendRank == 'Y'
				? (this.form.userSendRank = true)
				: (this.form.userSendRank = false);
			row.userExpressRank == 'Y'
				? (this.form.userExpressRank = true)
				: (this.form.userExpressRank = false);
			row.userQualityRank == 'Y'
				? (this.form.userQualityRank = true)
				: (this.form.userQualityRank = false);
		},
		deleteRow(index, rows) {
			rows.splice(index, 1);
		},
		/*  */
		subFrom() {
			this.dialogFormVisible = false;
			let form = this.form;
			let subData = this.roleData.filter(item => item.userName == form.userName);
			subData[0].auth = '111';
		},
		// 
		roleAdd() {
			this.clearFrom();
			this.dialogFormVisible = true;
		},
		// form
		clearFrom() {
			this.form.userName = '';
			this.form.userRealName = '';
			this.form.userSaleRank = false;
			this.form.userProRank = false;
			this.form.userSendRank = false;
			this.form.userExpressRank = false;
			this.form.userQualityRank = false;
		}
	},

	async mounted() {
		const data = await roleList();
		this.total = data.length;
		let l = data.length;
		for (var i = 0; i < l; i++) {
			let arr = new Array();
			data[i].userSaleRank == 'Y' ? arr.push('') : null;
			data[i].userProRank == 'Y' ? arr.push('') : null;
			data[i].userSendRank == 'Y' ? arr.push('') : null;
			data[i].userExpressRank == 'Y' ? arr.push('') : null;
			data[i].userQualityRank == 'Y' ? arr.push('') : null;
			data[i].userSuperRank == 'Y' ? arr.unshift(':') : null;
			data[i].auth = arr.join('  ');
		}
		this.roleData = data;
	}
};
</script>
