<template>
	<div>
		{{this.dataToday}}
		<div id="myChart" :style="{width: '300px', height: '300px'}"></div>
	</div>
</template>

<script>
	import {
		incomeAll
	} from "@/api"
	export default {
		name: 'hello',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App',
				dataToday: [],
			}
		},
		computed: {
			
		},
		mounted() {
			incomeAll().then(res => {
				this.dataToday=res
			}).then(()=>{
				this.drawLine(this.dataToday);
			})
		},
		methods: {
			drawLine(res) {
				// domecharts
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				myChart.clear();
				// 
				// 
				var option = {
					legend: {},
					tooltip: {},
					dataset: {
						// 
						source: this.echartData(res)
					},
					//  X category dataset 
					xAxis: {
						type: 'category'
					},
					//  Y 
					yAxis: {},
					//  bar  dataset 
					series: [{
							type: 'bar'
						},
						{
							type: 'bar'
						}
					]
				}
				// 
				myChart.setOption(option);
			},
			echartData(arr) {
				let source=[];
				source[0]=new Array("product","","");
				for (var i = 0; i < arr.length; i++) {
					let d=new Array();
					d.push(arr[i].name);
					d.push(arr[i].sumweight);
					d.push(arr[i].sum);
					source.push(d);
				}
				return source
			}
		}
	}
</script>
